.profile-form__btn-save {
  /*width: 100%;*/
  height: 50px;
  background-color: black;
  color: white;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  margin-top: 40px;
  outline: none;
  border: none;
  border-radius: 2px;
}

.profile-form__btn-save:hover {
  opacity: 0.8;
}

@media screen and (max-width: 530px){
  .profile-form__btn-save  {
    width: 210px;
    margin-left: calc((100% - 210px)/2);
  }
}

@media screen and (max-width: 900px) and (min-width: 530px){
  .profile-form__btn-save  {
    width: 83%;
    margin-left: calc((100% - 83%)/2);
  }
}

@media screen and (min-width: 900px) {
  .profile-form__btn-save {
    width: 358px;
    margin-left: calc((100% - 358px)/2);
  } 
}